@import 'style/common.css';
html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  line-height: 1.6;
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
/** env = windows **/
::-webkit-input-placeholder {
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
/** prevent default menu callout **/
a {
  -webkit-touch-callout: none;
}
.video-js {
  width: 100% !important;
  height: 100% !important;
}
body {
  background-color: #fbf9fe;
}
.avtar {
  width: 36px;
  height: 36px;
  position: absolute;
  right: 10px;
  top: 5px;
  overflow: hidden;
  border-radius: 50%;
  float: right;
}
.avtar img {
  width: 100%;
}
.vux-header {
  position: fixed;
}
#footer-bar {
  position: fixed;
  bottom: 0px;
}
.iconhome {
  font-size: 22px !important;
}
.iconfengge {
  font-size: 21px !important;
}
.iconmy {
  position: relative;
  top: -2px;
}
.weui-bar__item_on .iconhome:before {
  content: "\e6bb";
}
.weui-bar__item_on .iconfengge:before {
  content: "\e623";
}
.weui-bar__item_on .iconmy:before {
  content: "\e6f0";
}
.weui-bar__item_on .iconfont {
  color: #77b891 !important;
}
.weui-bar__item_on .weui-tabbar__label {
  color: #77b891 !important;
}
.vux-pop-out-enter-active,
.vux-pop-out-leave-active,
.vux-pop-in-enter-active,
.vux-pop-in-leave-active {
  will-change: transform;
  -webkit-transition: all 300ms;
  transition: all 300ms;
  height: 100%;
  width: 100%;
  top: 46px;
  left: 0px;
  position: fixed;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-perspective: 1000;
}
.vux-pop-out-enter {
  opacity: 0;
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
  height: 100%;
  width: 100%;
  top: 46px;
  left: 0px;
}
.vux-pop-out-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
  height: 100%;
  width: 100%;
  top: 46px;
  left: 0px;
}
.vux-pop-in-enter {
  opacity: 0;
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
  height: 100%;
  width: 100%;
  top: 46px;
  left: 0px;
}
.vux-pop-in-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
  height: 100%;
  width: 100%;
  top: 46px;
  left: 0px;
}



