
#listepage{background-color:#f5f5f5;min-height:600px;
}
.hot-course{overflow:hidden;background-color:#fff
}
.course-left{ width:32%;float:left;height:138px; background:url("../assets/course.png") no-repeat;background-size:cover;background-position:center
}
.course-right{ width:68%;float:left
}
.course-right>div{float:left; width:50%;text-align:center;border-bottom:1px solid #eeeeee;border-right:1px solid #eeeeee;box-sizing:border-box;height:46px; line-height:46px;color:#666;
}
.course-right>div:nth-child(5n){border-bottom:0px
}
.course-right>div:nth-child(6n){border-bottom:0px
}
.course-left h1{ font-size:18px;text-align:center;color:#666
}
.hot-course{border-bottom:1px solid #d8d7dc
}
.all-course-title{ line-height:36px;color:#808080;padding-left:20px;border-bottom:1px solid #eeeeee
}
.all-course{margin-top:20px;background-color:#fff
}
.all-course-con{overflow:hidden;background-color:#fff
}
.all-course-con>div{float:left; width:33.3333%;text-align:center;border-bottom:1px solid #eeeeee;border-right:1px solid #eeeeee;box-sizing:border-box;height:46px; line-height:46px;color:#666;background-color:#fff
}
.all-course-con>div:nth-child(3n){border-right:0px;
}



