
#homepage{padding-bottom:60px
}
.vux-search-fixed{position:static !important
}
.m-img {
  padding-bottom: 33%;
  display: block;
  position: relative;
  max-width: 100%;
  background-size: cover;
  background-position: center center;
  cursor: pointer;
  border-radius: 2px;
}
.m-title {
  color: #fff;
  text-align: center;
  text-shadow: 0 0 2px rgba(0, 0, 0, .5);
  font-weight: 500;
  font-size: 16px;
  position: absolute;
  left: 0;
  right: 0;
  width: 100%;
  text-align: center;
  top: 10px;
}
.m-time {
  color: #fff;
  text-shadow: 0 0 2px rgba(0, 0, 0, .5);
  float:left;
  text-align:left;
  font-size: 12px;
  padding-top: 4px;
  margin-left:10px;
  border-top: 1px solid #f0f0f0;
  margin-top: 5px;
  position:absolute;
  z-index:99;
}
.vux-masker-box{min-height:120px;
}
.masker-bottom{ width:100%;height:32px;position:absolute;bottom:0px;
}
.tags span{float:left;padding:2px 5px; background-color:#00a2d4; color:#fff;z-index:99;position:relative; margin-right:5px; font-size:14px;border-radius:4px;
}
.tags{float:right;margin-right:10px;
}
